body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.header{background-color:#fff;box-shadow:0px 10px 20px rgba(31,32,65,0.05)}.header__wrapper{max-width:1920px;display:flex;justify-content:space-between;align-items:center;padding:15px 9.72%;margin:0 auto}@media (max-width: 1200px){.header__wrapper{padding:15px 5%}}@media (max-width: 1020px){.header__wrapper{padding:15px 9.72%}}.header__burger::after{display:none;cursor:pointer;-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-color:#bc9cff;font-family:'material-icons';font-size:30px;content:'menu'}@media (max-width: 1020px){.header__burger::after{display:flex}}.header__burger_active{display:none}.header__nav{display:flex;-webkit-tap-highlight-color:rgba(0,0,0,0);align-items:center}.header__nav_active{display:flex}.header__menu{display:flex}.header__menu_active{position:absolute;top:0;right:0;flex-direction:column;background:#fff;box-shadow:0px 0px 25px rgba(0,0,0,0.2);border-radius:4px;padding:10px 20px;margin:0;z-index:3}.header__login-name{border-left:1px solid rgba(31,32,65,0.1);padding-left:31px;margin-left:30px}@media (max-width: 1020px){.header__login-name{display:none}}.header__login-name_active{display:flex;border-left:none;border-top:1px solid rgba(31,32,65,0.1);padding:10px 0;margin:5px 0 0}.header__buttons{display:flex;margin-left:28px}@media (max-width: 1020px){.header__buttons{display:none}}.header__buttons_active{display:flex;flex-direction:column;align-items:center;margin-top:10px;margin-left:0}.header__button-login{height:30px;margin-right:22px}@media (max-width: 1020px){.header__button-login{margin-right:0;margin-bottom:15px}}.header__button-reg{height:34px}@media (max-width: 1020px){.header__button-reg{margin-bottom:5px}}
