body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.footer{background-color:#fff;box-shadow:0px 10px 20px rgba(31,32,65,0.05)}.footer__content{display:grid;grid-template:auto/repeat(5, 1fr);grid-template-areas:'info menu menu menu subscription';justify-content:center;padding:100px 9.72% 96px;box-shadow:0px 1px 0px rgba(31,32,65,0.1)}@media (max-width: 1200px){.footer__content{padding:100px 5% 96px}}@media (max-width: 1023px){.footer__content{display:flex;flex-direction:column;align-items:center;padding:50px 5%}}.footer__info{grid-area:info;width:260px}@media (max-width: 1023px){.footer__info{margin-bottom:25px}}.footer__info-text{max-width:260px}@media (max-width: 1020px){.footer__info-text{text-align:center}}.footer__logo{margin-bottom:17px}@media (max-width: 1023px){.footer__logo{display:flex;justify-content:center}}.footer__menu{grid-area:menu;display:flex;flex:0.87;justify-content:space-between;padding:0 6%;margin-right:50px}@media (max-width: 1200px){.footer__menu{margin-right:0;flex:none}}@media (max-width: 1023px){.footer__menu{width:100%;margin-bottom:25px}}@media (max-width: 425px){.footer__menu{display:none}}.footer__menu-item{display:flex;flex-direction:column}.footer__subscription{grid-area:subscription;width:260px}@media (max-width: 1020px){.footer__subscription{text-align:center}}.footer__subscription .field{display:flex;align-items:center}.footer__subscription .field__arrow-icon{top:auto}.footer__subscription-title{font-size:12px;font-weight:bold;text-transform:uppercase;color:#1f2041;margin-bottom:13px}.footer__subscription-text{max-width:260px;margin-bottom:23px}.footer__line{border-top:1px solid #e5e5e5}.footer__copyright{max-width:1920px;margin:0 auto;display:flex;justify-content:space-between;padding:25px 9.72% 19px}@media (max-width: 1200px){.footer__copyright{padding:25px 5%}}@media (max-width: 560px){.footer__copyright{flex-direction:column;align-items:center}}.footer__copyright-text{text-align:center}.footer__copyright-social{display:flex;justify-content:space-between;width:114px}@media (max-width: 560px){.footer__copyright-social{margin-top:10px}}
