body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.dropdown{display:flex;align-items:center;position:relative}.dropdown::placeholder{color:rgba(31,32,65,0.25);font-size:14px;line-height:18px}.dropdown__selection{font-family:'montserrat', 'open-sans', sans-serif;color:rgba(31,32,65,0.75);font-size:14px;box-sizing:border-box;border-radius:4px;background:#ffffff;border:1px solid rgba(31,32,65,0.25);width:100%;height:auto;line-height:18px;padding:12px 88px 12px 15px;outline:none;text-overflow:ellipsis}.dropdown__selection::selection{background-color:none}.dropdown__selection::after{display:none}.dropdown__selection:focus,.dropdown__selection:hover{border:1px solid rgba(31,32,65,0.5);color:rgba(31,32,65,0.75)}.dropdown__selection_active{border:1px solid rgba(31,32,65,0.5);border-radius:4px 4px 0 0}.dropdown__icon{position:absolute;right:10px;font-size:24px;line-height:1px;color:rgba(31,32,65,0.5);cursor:pointer}.dropdown__icon_active{transform:rotate(180deg)}.dropdown__menu{visibility:hidden;opacity:0;position:absolute;background:#fff;left:0;z-index:99}.dropdown__menu-options{display:flex;justify-content:space-between;align-items:center;padding:0px 5px 7px 13px}.dropdown__item{font-size:12px;font-weight:800;color:#1f2041;text-transform:uppercase}.dropdown__item::selection{background-color:none}.dropdown__controls{display:flex}.dropdown__controls-decrement,.dropdown__controls-increment{border:1px solid rgba(31,32,65,0.25);box-sizing:border-box;border-radius:22px;height:auto;padding:7px;user-select:none;-webkit-tap-highlight-color:rgba(0,0,0,0)}.dropdown__controls-counter{width:34px;font-size:12px;font-family:'montserrat', 'open-sans', sans-serif;font-weight:bold;padding:0;border:none;text-align:center;outline:none}.dropdown__controls-counter::selection{background-color:none}.dropdown__menu_active{width:100%;padding-top:6px;border-radius:0 0 4px 4px;border:1px solid rgba(31,32,65,0.5);box-shadow:0px 10px 20px rgba(31,32,65,0.05);visibility:visible;opacity:1;transition:opacity 0.15s ease-in;top:42px;box-sizing:border-box}.dropdown__buttons{display:flex;padding:13px 7px 12px 13px;justify-content:space-between;user-select:none;-webkit-tap-highlight-color:rgba(0,0,0,0)}.dropdown__button{font-family:'montserrat', 'open-sans', sans-serif;font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;border:none;background:none;cursor:pointer}.dropdown__button_type_apply{color:#bc9cff;margin-left:auto}.dropdown__button_type_clear{visibility:visible;color:rgba(31,32,65,0.5)}.dropdown__button_hidden{visibility:hidden}.dropdown__button,.dropdown__button:active,.dropdown__button:focus{outline:none}.dropdown__controls-button{display:flex;justify-content:center;align-items:center;width:30px;height:30px;border:1px solid rgba(31,32,65,0.5);border-radius:50%;user-select:none;cursor:pointer}.dropdown__controls-button_disabled{opacity:0.5;cursor:default}
