body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.menu{display:flex;align-items:center}@media (max-width: 1020px){.menu{justify-content:center}}.menu__nav{display:flex}@media (max-width: 1020px){.menu__nav{display:none}}.menu__nav_active{top:0;right:0;display:flex;align-items:center;flex-direction:column;border-radius:4px;z-index:40}.menu__nav-item{position:relative;margin-left:20px}@media (max-width: 1020px){.menu__nav-item{margin:5px 0}}.menu__nav-item_more{display:flex}.menu__nav-link{display:flex;color:rgba(31,32,65,0.5);align-items:center;cursor:pointer}.menu__nav-link:active,.menu__nav-link:hover,.menu__nav-link:focus{color:#1f2041}.menu__nav-link_bold{font-weight:bold;color:rgba(31,32,65,0.75)}.menu__nav-arrow{font-size:24px}.menu__nav-arrow_expanded{transform:rotate(180deg)}.menu__submenu{display:none}.menu__submenu_active{display:block;position:absolute;top:40px;background-color:white;border-radius:4px;box-shadow:0 0 5px rgba(0,0,0,0.2);padding:10px 15px;z-index:20}@media (max-width: 1020px){.menu__submenu_active{right:100%;top:0}}.menu__submenu-item{display:flex;color:rgba(31,32,65,0.5);align-items:center;cursor:pointer;padding:5px 0}
