.search-room{max-width:1920px;margin:0 auto;display:grid;grid-template-columns:1fr 3fr;padding:26px 9.72% 73px}@media (max-width: 1024px){.search-room{grid-template-columns:1fr 2fr;padding:26px 5% 73px}}@media (max-width: 824px){.search-room{grid-template-columns:none;justify-items:center}}.search-room__sidebar{max-width:266px}.search-room__dropdown-quests{margin-top:20px}.search-room__dropdown-quests-title{margin-bottom:3px}.search-room__slider{margin-top:30px}.search-room__checkbox-buttons{margin-top:31px;max-width:230px}.search-room__checkbox-buttons-title{margin-bottom:10px}.search-room__rich-checkbox{margin-top:25px}.search-room__rich-checkbox-title{margin-bottom:15px}.search-room__rich-checkbox-wrapper{max-width:260px;margin:10px 0}.search-room__dropdown-comfort{margin-top:30px}.search-room__dropdown-comfort-title{margin-bottom:3px}.search-room__checkbox-list{margin-top:26px}.search-room__samples{display:grid;padding:4px 0 0 35px}@media (max-width: 824px){.search-room__samples{padding:4px 0 0 0}}.search-room__samples-header{margin-bottom:20px}.search-room__samples-wrapper{display:grid;justify-items:center;grid-template-columns:repeat(auto-fill, minmax(270px, 1fr));row-gap:19px;column-gap:12px;margin-bottom:39px}.search-room__pagination{justify-self:center;margin-right:14px}
