body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.sample{max-width:270px;background:#ffffff;box-shadow:0px 10px 20px rgba(31,32,65,0.05);border-radius:4px;user-select:none;-webkit-tap-highlight-color:rgba(0,0,0,0)}.sample__image{position:relative}.sample__info{padding:20px 18px 14px 20px}.sample__price{display:flex;align-items:baseline;padding-bottom:8px;margin-bottom:8px;border-bottom:1px solid rgba(31,32,65,0.1)}.sample__price-numeration{font-family:'quicksand', 'open-sans', sans-serif;font-weight:bold;font-size:14px;line-height:17px;color:#1f2041;margin-right:5px}.sample__price-number{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:19px;line-height:24px;color:#1f2041;margin-right:5px}.sample__price-luxury{font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;color:#bc9cff}.sample__price-cost{font-weight:bold;font-size:14px;color:rgba(31,32,65,0.5);margin-left:auto;margin-right:5px}.sample__price-inday{font-size:12px;line-height:18px;color:rgba(31,32,65,0.5)}.sample__reviews{display:flex}.sample__reviews-number{font-weight:bold;font-size:14px;color:rgba(31,32,65,0.5);margin-left:auto;margin-right:4px}.sample__reviews-text{font-size:14px;color:rgba(31,32,65,0.5);margin-right:2px}
