body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.room-details{margin:0 auto;max-width:1920px}.room-details__content{display:grid;grid-template-columns:2fr 1fr;column-gap:56px;padding:70px 140px 62px;margin:0 auto;max-width:1200px}@media (max-width: 1360px){.room-details__content{grid-template-columns:none;justify-items:center}}@media (max-width: 930px){.room-details__content{padding:70px}}@media (max-width: 540px){.room-details__content{padding:70px 0}}.room-details__info{display:grid;grid-template-columns:1fr 1fr}@media (max-width: 1360px){.room-details__info{margin-bottom:40px}}@media (max-width: 930px){.room-details__info{display:flex;flex-direction:column;align-items:center}}.room-details__feature{max-width:290px}@media (max-width: 930px){.room-details__feature{margin-bottom:30px}}.room-details__feature-header{letter-spacing:-0.6px;margin-bottom:20px}.room-details__feature-wrapper .feature:last-child{margin-bottom:0;padding-bottom:0;border-bottom:none}.room-details__impressions{max-width:321px}@media (max-width: 930px){.room-details__impressions{margin-bottom:30px;width:320px}}.room-details__impressions-header{letter-spacing:-0.6px}@media (max-width: 400px){.room-details__impressions-header{padding:0 20px}}.room-details__diagram{padding:17px 11px 0 0}.room-details__reviews{grid-column:1/3;padding-right:14px;margin-top:33px;margin-bottom:10px;max-width:712px}@media (max-width: 930px){.room-details__reviews{grid-column:auto;margin-right:0;margin-bottom:30px}}@media (max-width: 540px){.room-details__reviews{padding:0 20px}}.room-details__reviews-header{display:flex;justify-content:space-between;margin-bottom:20px}@media (max-width: 430px){.room-details__reviews-header{flex-direction:column}}.room-details__reviews-title{letter-spacing:-0.6px}.room-details__reviews-number{font-size:14px;color:rgba(31,32,65,0.5)}@media (max-width: 430px){.room-details__reviews-number{align-self:flex-end}}.room-details__comment{margin-bottom:23px}.room-details__bullet-list{max-width:260px}@media (max-width: 930px){.room-details__bullet-list{margin-bottom:30px}}.room-details__bullet-list-header{letter-spacing:-0.6px;margin-left:20px;margin-bottom:16px}.room-details__bullet-list-wrapper{margin-bottom:10px}.room-details__cancellation{max-width:340px}@media (max-width: 400px){.room-details__cancellation{max-width:auto;padding:0 20px}}.room-details__cancellation-header{letter-spacing:-0.6px;margin-bottom:16px}.room-details__card{width:380px}@media (max-width: 540px){.room-details__card{width:320px}}
