body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.slider{user-select:none;-webkit-tap-highlight-color:rgba(0,0,0,0)}.slider__header{display:flex;margin-bottom:23px}.slider__title{margin-right:auto}.slider__val{width:110px;font-size:12px;border:none;color:rgba(31,32,65,0.5);font-family:'montserrat', 'open-sans', sans-serif;line-height:15px;letter-spacing:1.2px;text-align:right}.slider__footnote{font-size:12px;line-height:14px;margin-top:10px;color:rgba(31,32,65,0.5)}.ui-slider{position:relative}.ui-slider .ui-slider-handle{position:absolute;z-index:2;width:14px;height:14px;cursor:pointer;background:linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%);border:2px solid #ffffff;border-radius:10px;user-select:none;outline:none}.ui-slider .ui-slider-range{position:absolute;z-index:1;font-size:0.7em;display:block;overflow:hidden}.ui-slider-horizontal{height:4px}.ui-slider-horizontal .ui-slider-handle{margin-left:-6px}.ui-slider-horizontal .ui-slider-range{height:6px}.ui-slider-horizontal .ui-slider-range-min{left:0}.ui-slider-horizontal .ui-slider-range-max{right:0}.ui-widget-content{border:1px solid rgba(31,32,65,0.25);background:#fff;cursor:pointer}.ui-widget-header{background:linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%)}.ui-corner-all{-moz-border-radius:4px;-webkit-border-radius:4px;border-radius:4px;display:flex;align-items:center}
