body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.radio-buttons{display:flex;justify-content:space-between}.radio-buttons__label{position:relative;padding-left:30px;cursor:pointer;-webkit-tap-highlight-color:rgba(0,0,0,0)}.radio-buttons__input:checked,.radio-buttons__input:not(:checked){display:none}.radio-buttons__input:checked ~ .radio-buttons__marker::after{content:'';position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:12px;height:12px;box-sizing:border-box;background-image:linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);border-radius:100%}.radio-buttons__marker{content:'';position:absolute;top:0;left:0;width:20px;height:20px;border-radius:100%;border:1px solid rgba(31,32,65,0.25);box-sizing:border-box}
