body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.field{position:relative}.field__header{display:flex;justify-content:space-between;align-items:center;line-height:24px}.field__title{line-height:24px}.field__note{font-size:12px;text-transform:uppercase}.field__input{font-family:'montserrat', 'open-sans', sans-serif;box-sizing:border-box;border-radius:4px;background:#ffffff;border:1px solid rgba(31,32,65,0.25);width:100%;line-height:18px;padding:12px 15px;color:rgba(31,32,65,0.75);letter-spacing:0.02rem;outline:none}.field__input::placeholder{color:rgba(31,32,65,0.25);font-size:14px;line-height:18px}.field__input:focus,.field__input:hover{border:1px solid rgba(31,32,65,0.5);color:rgba(31,32,65,0.75)}.field__expand-more-icon{position:absolute;top:35px;right:10px;font-size:24px;line-height:24px;color:rgba(31,32,65,0.5);cursor:pointer}.field__arrow-icon{position:absolute;top:35px;right:10px;font-size:24px;line-height:24px;background:linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);cursor:pointer;-webkit-background-clip:text;-webkit-text-fill-color:transparent}
