.diagram{user-select:none;-webkit-tap-highlight-color:rgba(0,0,0,0)}.diagram__figure{display:flex}.diagram__wrap,.diagram__legend{align-self:center}.diagram__sector{cursor:pointer}.diagram__label{font-size:16px;fill:#000;-moz-transform:translateY(0.25em);-ms-transform:translateY(0.25em);-webkit-transform:translateY(0.25em);transform:translateY(0.25em)}.diagram__value{font-size:0.4em;font-weight:bold;line-height:1;text-anchor:middle;-moz-transform:translateY(-0.25em);-ms-transform:translateY(-0.25em);-webkit-transform:translateY(-0.25em);transform:translateY(-0.25em)}.diagram__unit{font-size:0.2em;font-weight:bold;text-transform:uppercase;text-anchor:middle;-moz-transform:translateY(0.7em);-ms-transform:translateY(0.7em);-webkit-transform:translateY(0.7em);transform:translateY(0.7em)}.diagram__item{display:flex;align-items:center;cursor:pointer}.diagram__circle{width:10px;height:10px;border-radius:50%;margin-right:5px}.diagram__circle_theme_satisfactorily{background:linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%)}.diagram__circle_theme_good{background:linear-gradient(180deg, #6fcf97 0%, #66d2ea 100%)}.diagram__circle_theme_perfectly{background:linear-gradient(180deg, #ffe39c 0%, #ffba9c 100%)}.diagram__circle_theme_disappointed{background:linear-gradient(180deg, #919191 0%, #3d4975 100%)}
