body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.booking{border:1px solid rgba(0,0,0,0.12);box-sizing:border-box;box-shadow:0px 0px 25px rgba(0,0,0,0.2);border-radius:4px;background:#ffffff;padding:39px 29px 29px;margin-bottom:40px}@media (max-width: 425px){.booking{padding:20px 15px}}.booking__header{display:flex;align-items:baseline}.booking__numeration{font-family:'quicksand', 'open-sans', sans-serif;font-weight:bold;font-size:14px;line-height:17px;color:#1f2041;margin-right:6px}.booking__number{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-right:5px}.booking__luxury{font-weight:bold;font-size:12px;line-height:15px;text-transform:uppercase;color:#bc9cff}.booking__cost{font-weight:bold;font-size:14px;line-height:18px;color:rgba(31,32,65,0.5);margin-left:auto;margin-right:5px}.booking__inday{font-size:12px;line-height:18px;color:rgba(31,32,65,0.5)}.booking__date-range{display:flex;margin-top:16px}.booking__date-range .field:first-child{margin-right:20px}@media (max-width: 425px){.booking__date-range .field:first-child{margin-right:10px}}.booking__guests{margin-top:19px}.booking__guests-title{margin-bottom:4px}.booking__price{display:grid;grid-auto-rows:minmax(34px, auto);line-height:18px;margin-top:21px}.booking__price-item{display:flex;align-items:flex-start;margin-bottom:10px}.booking__price-name{max-width:214px}.booking__price-info{width:18px;font-weight:bold;text-align:center;border:1px solid rgba(31,32,65,0.25);border-radius:10px;line-height:18px;color:rgba(31,32,65,0.25);margin-left:5px}.booking__price-cost{margin-left:auto}.booking__total{display:flex;justify-content:space-between;align-items:baseline;font-weight:bold;font-size:19px;color:#1f2041;margin-top:20px}.booking__total-title{margin-right:5px}.booking__total-points{flex:1;border-bottom:1px dotted rgba(31,32,65,0.25)}.booking__total-cost{transform:scaleX(0.85);transform-origin:100% 0}.booking__button{height:44px;margin-top:20px}
