body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.checkbox{display:block;position:relative;padding-left:30px;cursor:pointer;user-select:none;-webkit-tap-highlight-color:rgba(0,0,0,0);line-height:18px;color:rgba(31,32,65,0.5)}.checkbox__input[type='checkbox']{display:none}.checkbox__marker{position:absolute;top:0;left:0;width:18px;height:18px;border:1px solid rgba(31,32,65,0.25);border-radius:0.25em}.checkbox__marker::after{content:'';display:none;position:absolute;top:4px;left:6px;width:4px;height:6px;border-width:0 2px 2px 0;border-style:solid;border-image:linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);border-image-slice:1;transform:rotate(45deg)}.checkbox__input:checked+.checkbox__marker{border-color:#bc9cff}.checkbox__input:checked+.checkbox__marker::after{display:block}
