body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.like-button{display:flex;align-items:center;width:fit-content;border-width:1px;border-radius:10px;border-style:solid;border-color:rgba(31,32,65,0.25);padding:3px 10px 3px 6px;cursor:pointer}.like-button__icon{font-size:10px;margin-right:5px;background:rgba(31,32,65,0.25);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.like-button__icon_theme_liked{background:#bc9cff;-webkit-background-clip:text;-webkit-text-fill-color:transparent}.like-button__counter{font-size:10px;line-height:12px;color:rgba(31,32,65,0.25)}.like-button__counter_theme_liked{color:#bc9cff}.like-button_theme_liked{border-color:#bc9cff}
