body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.button{display:flex;justify-content:center;align-items:center;height:100%;padding:0 19px;font-family:'montserrat', 'open-sans', sans-serif;font-weight:bold;font-size:12px;color:#fff;text-transform:uppercase;background:linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);border:none;border-radius:22px;cursor:pointer}.button_theme_border{color:#bc9cff;background:#fff;border:2px solid #bc9cff;padding:0 17px}.button_disabled{opacity:0.5;cursor:auto}.button_size_small{padding:0;background:none;color:#bc9cff}.button_size_small+.button_theme_disabled{color:rgba(31,32,65,0.5)}.button_size_long{position:relative;display:flex;justify-content:center;align-items:center;width:100%}.button__icon{font-size:24px;line-height:24px;position:absolute;color:#FFFFFF;right:3%}.button:hover{opacity:0.5}
