body{margin:0;font-family:'montserrat', 'open-sans', sans-serif;font-size:14px;line-height:24px;color:rgba(31,32,65,0.75);font-style:normal}h1{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:24px;line-height:30px;color:#1f2041;margin-block-start:0;margin-block-end:0}h2{font-family:'open-sans', 'montserrat', sans-serif;font-weight:bold;font-size:20px;color:#1f2041;margin-block-start:0;margin-block-end:0}h3{font-weight:bold;font-size:12px;line-height:17px;text-transform:uppercase;color:#1f2041;margin-block-start:0;margin-block-end:0}th{font-weight:bold}p{margin-block-start:0;margin-block-end:0}ul{margin-block-start:0;margin-block-end:0}input[type='date']{padding-inline-start:0}.pagination{max-width:241px}.pagination__wrapper{display:flex;justify-content:space-between;margin-bottom:8px}.pagination__item{width:40px;height:40px;font-size:12px;text-align:center;color:rgba(31,32,65,0.5);line-height:40px;border-radius:22px;cursor:pointer}.pagination__item_theme_current{font-weight:bold;color:#FFFFFF;background:linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%)}.pagination__item_theme_next{background:linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%)}.pagination__item_theme_next:after{cursor:pointer;-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-color:#fff;font-family:'material-icons';font-size:24px;content:'arrow_forward'}.pagination__footnote{text-align:center}
